.row {
  text-align: center;
  display: flex;
  justify-content: center;

  &__item {
    display: block;
    width: 52px;
    height: 52px;
    border: 2px solid #bbb;
    margin: 2.5px;
    text-align: center;
    line-height: 52px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2.5em;

    &:nth-child(2) {
      animation-delay: 0.2s;
    }

    &:nth-child(3) {
      animation-delay: 0.4s;
    }

    &:nth-child(4) {
      animation-delay: 0.6s;
    }

    &:nth-child(5) {
      animation-delay: 0.8s;
    }
  }
}

.grey {
  --background-color: #3a3a3c;
  --border-color: #3a3a3c;
  animation: rotate 0.5s ease forwards;
}

.green {
  --background-color: #538d4e;
  --border-color: #538d4e;
  animation: rotate 0.5s ease forwards;
}

.yellow {
  --background-color: #b59f3b;
  --border-color: #b59f3b;
  animation: rotate 0.5s ease forwards;
}

.current > .filled {
  animation: bounce 0.2s ease-in-out forwards;
}

@keyframes rotate {
  0% {
    transform: rotateX(0);
    background-color: #fff;
    border-color: #333;
  }

  45% {
    transform: rotateX(90deg);
    background-color: #fff;
    border-color: #333;
  }

  55% {
    transform: rotateX(90deg);
    background-color: var(--background-color);
    border-color: var(--border-color);
    color: #fff;
  }

  100% {
    transform: rotateX(0deg);
    background-color: var(--background-color);
    border-color: var(--border-color);
    color: #fff;
  }
}

@keyframes bounce {
  0% {
    transform: scale(1);
    border-color: #bbb;
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
    border-color: #333;
  }
}